import React from "react";
import { animated } from "react-spring";
import { GatsbyLocation } from "local-types";

import Awesome from "../components/Awesome";
import Layout from "../components/Layout";
import SEO from "../components/Seo";
import { useSiteMetadata, usePageTransitions } from "../hooks";

interface AwesomePageProps {
  location: GatsbyLocation;
}

const AwesomePage: React.FunctionComponent<AwesomePageProps> = ({
  location,
}): React.ReactElement => {
  const { title: siteTitle, author } = useSiteMetadata();

  const transitions = usePageTransitions({ location });

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Amazing World" />
      {transitions.map(({ props, key }) => (
        <animated.div key={key} style={props}>
          <Awesome />
        </animated.div>
      ))}
    </Layout>
  );
};

export default AwesomePage;
